<template>
  <div class="showBlogsItem">
    <headerItem></headerItem>

    <div class="content">
      <div v-if="entid">
        <el-row>
          <el-col :span="12" :offset="6">
            <div class="search">
              <el-form ref="form" :model="searchItem" label-width="100px">
                <!-- <el-form-item> -->
                <el-input
                  prop="text"
                  v-model="searchItem.otherInfo"
                  placeholder="请输入期望职位或期望工作城市"
                  style="margin-left:50px;margin-bottom:20px;"
                >
                  <el-button slot="append" icon="el-icon-search" @click="funSearch"></el-button>
                </el-input>
                <!-- </el-form-item> -->
                <el-form-item label="城市">
                  <el-radio-group
                    v-model="searchItem.jobAdd"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="全国"></el-radio-button>
                    <el-radio-button label="北京"></el-radio-button>
                    <el-radio-button label="上海"></el-radio-button>
                    <el-radio-button label="广州"></el-radio-button>
                    <el-radio-button label="深圳"></el-radio-button>
                    <el-radio-button label="杭州"></el-radio-button>
                    <el-radio-button label="南京"></el-radio-button>
                    <el-radio-button label="武汉"></el-radio-button>
                    <el-radio-button label="成都"></el-radio-button>
                    <el-radio-button label="其他"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="性质">
                  <el-radio-group
                    v-model="searchItem.jobType"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="全职"></el-radio-button>
                    <el-radio-button label="兼职"></el-radio-button>
                    <el-radio-button label="实习"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="薪资">
                  <el-radio-group
                    v-model="searchItem.jobPay"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="面议"></el-radio-button>
                    <el-radio-button label="5-8W"></el-radio-button>
                    <el-radio-button label="8-12W"></el-radio-button>
                    <el-radio-button label="12-18W"></el-radio-button>
                    <el-radio-button label="18-20W"></el-radio-button>
                    <el-radio-button label="20W以上"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="经验">
                  <el-radio-group
                    v-model="searchItem.workExp"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="不限"></el-radio-button>
                    <el-radio-button label="应届"></el-radio-button>
                    <el-radio-button label="1-3年"></el-radio-button>
                    <el-radio-button label="3-5年"></el-radio-button>
                    <el-radio-button label="5-8年"></el-radio-button>
                    <el-radio-button label="8年以上"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="学历">
                  <el-radio-group
                    v-model="searchItem.degree"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="不限"></el-radio-button>
                    <el-radio-button label="大专"></el-radio-button>
                    <el-radio-button label="本科"></el-radio-button>
                    <el-radio-button label="硕士"></el-radio-button>
                    <el-radio-button label="博士"></el-radio-button>
                    <el-radio-button label="其他"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
              </el-form>
              <span class="total">共{{psnList.length}}条数据</span>
            </div>
          </el-col>
        </el-row>
        <div v-if="psnList.length==0" style="text-align:center;">
          <span style="font-size:14px;color:#909399">暂无数据</span>
        </div>
        <div v-else v-for="psn in tempList" :key="psn.resumeid" class="singleItem">
          <el-row :gutter="20">
            <el-col :span="12" :offset="6">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <router-link
                    :to="{name: 'singlepsn', query:{resumeid: psn.resumeid,psnid:psn.psnid}}"
                    target="_blank"
                  >
                    {{psn.name}}
                    <span class="update-time">({{psn.updateTime}})</span>
                    <span class="ent-name">{{psn.nowStatus}}</span>
                    <p class="psn-item">
                      <span class="pay">{{psn.jobPay}}</span>|
                      <span>{{psn.workExp}}</span>|
                      <span>{{psn.jobAdd}}</span>|
                      <span>{{psn.degree}}</span>
                    </p>
                    <p class="psn-item">
                      <span>期望职位:{{psn.jobName}}</span>
                    </p>
                  </router-link>
                </div>
                <div>
                  <el-tag type="success" v-for="(tag,index) in psn.psnTag" :key="index">{{tag}}</el-tag>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <!-- <el-row :gutter="20">
            <el-col :span="12" :offset="6">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <router-link
                    :to="{name: 'singlepsn', query:{resumeid: psn.resumeid,psnid:psn.psnid}}"
                    target="_blank"
                  >
                    <span>{{psn.name}}</span>
                  </router-link>
                </div>
                <div></div>
              </el-card>
            </el-col>
          </el-row>-->
        </div>
      </div>
      <div v-else>
        <el-row>
          <el-col :span="12" :offset="6">
            <div class="search">
              <el-form ref="form" :model="searchItem" label-width="100px">
                <!-- <el-form-item> -->
                <el-input
                  prop="text"
                  v-model="searchItem.otherInfo"
                  placeholder="请输入职位名或公司名"
                  style="margin-left:50px;margin-bottom:20px;"
                >
                  <el-button slot="append" icon="el-icon-search" @click="funSearch"></el-button>
                </el-input>
                <!-- </el-form-item> -->
                <el-form-item label="性质">
                  <el-radio-group
                    v-model="searchItem.jobType"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="全职"></el-radio-button>
                    <el-radio-button label="兼职"></el-radio-button>
                    <el-radio-button label="实习"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="薪资">
                  <el-radio-group
                    v-model="searchItem.jobPay"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="面议"></el-radio-button>
                    <el-radio-button label="5-8W"></el-radio-button>
                    <el-radio-button label="8-12W"></el-radio-button>
                    <el-radio-button label="12-18W"></el-radio-button>
                    <el-radio-button label="18-20W"></el-radio-button>
                    <el-radio-button label="20W以上"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="经验">
                  <el-radio-group
                    v-model="searchItem.workExp"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="不限"></el-radio-button>
                    <el-radio-button label="应届"></el-radio-button>
                    <el-radio-button label="1-3年"></el-radio-button>
                    <el-radio-button label="3-5年"></el-radio-button>
                    <el-radio-button label="5-8年"></el-radio-button>
                    <el-radio-button label="8年以上"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="学历">
                  <el-radio-group
                    v-model="searchItem.degree"
                    text-color="#fff"
                    fill="#67c23a"
                    size="small"
                    @change="funSearch"
                  >
                    <el-radio-button label="不限"></el-radio-button>
                    <el-radio-button label="大专"></el-radio-button>
                    <el-radio-button label="本科"></el-radio-button>
                    <el-radio-button label="硕士"></el-radio-button>
                    <el-radio-button label="博士"></el-radio-button>
                    <el-radio-button label="其他"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
              </el-form>
              <span class="total">共{{jobList.length}}条数据</span>
            </div>
          </el-col>
        </el-row>
        <div v-if="jobList.length==0" style="text-align:center;">
          <span style="font-size:14px;color:#909399">暂无数据</span>
        </div>
        <div v-else v-for="job in tempList" :key="job.jobid" class="singleItem">
          <el-row :gutter="20">
            <el-col :span="12" :offset="6">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <router-link :to="{name: 'singlejob', query:{jobid: job.jobid}}" target="_blank">
                    <span>
                      {{job.jobName}}
                      <span class="update-time">({{job.updateTime}})</span>
                    </span>
                    <span class="ent-name">{{job.entName}}</span>
                    <br>
                    <span class="pay">{{job.jobPay}}</span>|
                    <span>{{job.degree}}</span>|
                    <span>{{job.workExp}}</span>
                  </router-link>
                </div>
                <div>
                  <el-tag type="success" v-for="(tag,index) in job.entTag" :key="index">{{tag}}</el-tag>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
        <el-pagination
          layout="prev, pager, next"
          style="text-align:center;"
          class="page"
          @size-change="funSizeChange"
          @current-change="funCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
        ></el-pagination>
      </div>
    </div>
    <footerItem></footerItem>
  </div>
</template>

<script>
import Cookies from "vue-cookies";
import headerItem from "./childPageHeader";
import footerItem from "./childPageFooter";
export default {
  name: "showBlogsItem",
  components: {
    headerItem,
    footerItem
  },
  data() {
    return {
      total: 1,
      currentPage: 1,
      pageSize: 10,
      tempList: [],
      psnid: "",
      entid: "",
      jobList: [
        {
          jobid: "",
          jobName: "",
          jobPay: "",
          workExp: "",
          jobType: "",
          entTag: []
        }
      ],
      psnList: [],
      // otherInfo: "",
      searchItem: {
        otherInfo: "",
        jobPay: "",
        workExp: "",
        degree: "",
        jobType: ""
      }
    };
  },
  created() {
    window.scroll(0, 0);
    this.searchItem.otherInfo = this.$route.query.item;
    if (Cookies.get("entid")) {
      this.psnid = "";
      this.entid = Cookies.get("entid");
      this.axios
        .post("searchPsn", {
          key: "0000",
          type: "all"
        })
        .then(data => {
          if (data.data.status == "ok") {
            let arr = JSON.parse(data.data.msg);
            let res = [];
            for (let key in arr) {
              res[key] = arr[key].fields;
              res[key].resumeid = arr[key].pk;
              res[key].psnTag = res[key].psnTag.split(",");
              switch (res[key].nowStatus) {
                case "0":
                  res[key].nowStatus = "待沟通";
                  break;
                case "1":
                  res[key].nowStatus = "学生";
                  break;
                case "2":
                  res[key].nowStatus = "观望机会";
                  break;
                case "3":
                  res[key].nowStatus = "求职中";
                  break;
                case "4":
                  res[key].nowStatus = "暂不考虑";
                  break;
              }
              // res[key].resumeid = arr[key].pk;
            }
            this.psnList = res;
            this.tempList = this.psnList.slice(0, 10);
            this.total = this.psnList.length;
          }
        });
    } else {
      this.entid = "";
      this.psnid = Cookies.get("psnid");
      this.axios
        .post("searchJob", {
          msg: {},
          key: "0000",
          type: "all"
        })
        .then(data => {
          if (data.data.status == "ok") {
            let arr = JSON.parse(data.data.msg);
            let res = [];
            for (let key in arr) {
              res[key] = arr[key].fields;
              res[key].jobid = arr[key].pk;
              res[key].entTag = res[key].entTag.split(",");
            }
            this.jobList = res;
            this.tempList = this.jobList.slice(0, 10);
            this.total = this.jobList.length;
          }
        });
    }
  },
  methods: {
    funSearch() {
      if (Cookies.get("entid")) {
        this.axios
          .post("searchPsn", {
            msg: this.searchItem,
            key: "0000",
            type: "search"
          })
          .then(data => {
            if (data.data.status == "ok") {
              let arr = JSON.parse(data.data.msg);
              let res = [];
              for (let key in arr) {
                res[key] = arr[key].fields;
                res[key].resumeid = arr[key].pk;
                res[key].psnTag = res[key].psnTag.split(",");
                switch (res[key].nowStatus) {
                  case "0":
                    res[key].nowStatus = "待沟通";
                    break;
                  case "1":
                    res[key].nowStatus = "学生";
                    break;
                  case "2":
                    res[key].nowStatus = "观望机会";
                    break;
                  case "3":
                    res[key].nowStatus = "求职中";
                    break;
                  case "4":
                    res[key].nowStatus = "暂不考虑";
                    break;
                }
              }
              this.psnList = res;
              this.tempList = this.psnList.slice(0, 10);
              this.total = this.psnList.length;
            }
          });
      } else {
        this.axios
          .post("searchJob", {
            msg: this.searchItem,
            key: "0000",
            type: "search"
          })
          .then(data => {
            if (data.data.status == "ok") {
              let arr = JSON.parse(data.data.msg);
              let res = [];
              for (let key in arr) {
                res[key] = arr[key].fields;
                res[key].jobid = arr[key].pk;
                res[key].entTag = res[key].entTag.split(",");
              }
              this.jobList = res;
              this.tempList = this.jobList.slice(0, 10);
              this.total = this.jobList.length;
            }
          });
      }
    },
    funSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.funCurrentChange(this.currentPage);
    },
    funCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.currentChangePage(this.jobList, currentPage);
    },
    //分页方法
    currentChangePage(list, currentPage) {
      let from = (currentPage - 1) * this.pageSize;
      let to = currentPage * this.pageSize;
      this.tempList = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tempList.push(list[from]);
        }
      }
    }
  }
};
</script>
<style scoped>
.content {
  padding: 60px;
  color: #303133;
}
.content .title {
  color: #303133;
  text-align: center;
  font-size: 18px;
  text-decoration: #303133;
}
.singleItem {
  margin: 30px 0;
}
.singleItem a {
  color: #303133;
  text-decoration: none;
}
p {
  font-size: 14px;
}
.btn {
  text-align: right;
}
.update-time {
  color: #909399;
  font-size: 14px;
}
.pay {
  color: #f56c6c;
}
.ent-name {
  float: right;
}
.el-tag {
  margin: 10px;
}
.el-form-item {
  margin-bottom: 10px;
}
.search {
  border-bottom: 1px solid #ddd;
}
.search .total {
  color: #909399;
  font-size: 12px;
}
.search .el-form {
  padding-bottom: 20px;
}
</style>
